.Spinner {
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: rgb(58, 62, 100);
}

.Spinner .Loader {
  font-size: 20px;
  color: rgb(207, 70, 71);
}



