.Remover {
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

.Remover-Schedule {
  height: 14px;
  width: 14px;
  font-size: 1.2rem;
  color: black;
}

.Remover-Schedule:hover {
  color: rgb(207, 70, 71);
}

.Remover-Schedule-White {
  color: white;
}

.Remover-Employee {
  height: 40px;
  width: 40px;
  font-size: 1.4rem;
  line-height: 1.4rem;
}

.Remover-Employee:hover {
  color: white;
  -webkit-box-shadow: 0 0 8px rgb(207, 70, 71);
          box-shadow: 0 0 8px rgb(207, 70, 71);
}



