.Editor {
  position: relative;
  padding: 18px;
  height: calc(100% - 60px);
  overflow: hidden;
}

.Editor__Baskets {
  position: absolute;
  top: 18px;
  right: 18px;
}

.Editor__Baskets .Basket {
  margin-bottom: 20px;
}

.Editor .Details {
  position: absolute;
  bottom: 20px;
  right: 20px;
}



