.Detail {
  background: white;
  width: 160px;
  height: 50px;
  padding: 2px;
  position: relative;
  font-size: 0.8rem;
  margin: 2px;
  border: 2px dashed #e6e6e6;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.Detail-Filled {
  border: none;
  border: 2px solid #e6e6e6;
  cursor: pointer;
}

.Detail__Pointer {
  position: absolute;
  height: 10px;
  width: 10px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  background: #e6e6e6;
  border-radius: 50%;
}

.Detail .Remover {
  position: absolute;
  top: -1px;
  right: 0px;
  display: none;
}

.Detail:hover .Remover {
  display: -ms-flexbox;
  display: flex;
}


