.Employee {
  display: inline-block;
  padding: 0 2px;
  position: relative;
  background: white;
  cursor: -webkit-grab;
  cursor: grab;
  font-size: 0.8rem;
  width: 110px;
  height: 50px;
  margin: 2px;
  border: 2px solid #e6e6e6;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  -webkit-box-shadow: 0 0 8px #e6e6e6;
          box-shadow: 0 0 8px #e6e6e6;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-pack: end;
      justify-content: flex-end;
  font-weight: 900;
}

.Employee:hover {
  border: 2px solid #b3b3b3;
}

.Employee-ConfirmedDayOff {
  color: #6a8759;
}

.Employee-ConfirmedDayOff:hover {
  border: 2px solid #6a8759;
  -webkit-box-shadow: 0 0 8px #6a8759;
          box-shadow: 0 0 8px #6a8759;
}

.Employee-PendingDayOff {
  color: rgb(195, 154, 78);
}

.Employee-PendingDayOff:hover {
  border: 2px solid rgb(195, 154, 78);
  -webkit-box-shadow: 0 0 8px rgb(195, 154, 78);
          box-shadow: 0 0 8px rgb(195, 154, 78);
}

.Employee-Schedule {
  color: rgb(58, 62, 100);
}

.Employee-Schedule:hover {
  border: 2px solid rgb(58, 62, 100);
  -webkit-box-shadow: 0 0 8px rgb(58, 62, 100);
          box-shadow: 0 0 8px rgb(58, 62, 100);
}



