.Employees__OuterContainer {
  height: calc(100% - 60px);
  overflow: hidden;
}

.Employees {
  position: relative;
  padding: 60px;
  height: 100%;
  overflow-y: scroll;
  margin-right: -17px;
}

.Employees__InnerContainer {
  width: 400px;
  margin: auto;
}

.Employees__Header {
  background: #e6e6e6;
  height: 60px;
  padding: 0 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.Employees__Header input {
  width: calc(100% - 66px);
}

.Employees__Button {
  background: rgb(207, 70, 71);
  color: white;
  width: 66px;
  cursor: not-allowed;
}

.Employees__Button-Active {
  cursor: pointer;
}

.Employees__Button-Active:hover {
  -webkit-box-shadow: 0 0 8px rgb(207, 70, 71);
          box-shadow: 0 0 8px rgb(207, 70, 71);
}

.Employees__Item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background: #e6e6e6;
  margin-top: 10px;
  height: 60px;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  padding: 10px;
}

.Employees__Item:hover {
  background: #b3b3b3;
}

.Employees__Item:hover .Remover-Employee {
  background: rgb(207, 70, 71);
  color: white;
}

.Employees__Item__Name {
  margin-left: 15px;
}



