.Navigation {
  display: -ms-flexbox;
  display: flex;
  height: 100%;
}

.Navigation ul {
  margin: 0;
  padding: 0;
  height: 100%;
}

.Navigation ul li {
  display: inline-block;
  list-style-type: none;
  height: 100%;
}

.Navigation ul li a {
  color: white;
  font-size: 1.2rem;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  cursor: pointer;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  padding: 0 10px;
}

.Navigation ul li a:hover {
  background: rgba(255, 255, 255, 0.1);
}

.Navigation ul li a.active {
  color: rgba(255, 255, 255, 1);
  border-bottom: 5px solid rgb(207, 70, 71);
}



