@font-face {
  font-family: open-sans;
  src: url("./assets/fonts/OpenSans-Regular.ttf");
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: open-sans, sans-serif;
}

button {
  font-family: inherit;
  cursor: pointer;
  font-size: 1.2rem;
  height: 40px;
  border: none;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  position: relative;
  padding: 0 15px;
}

input {
  font-family: inherit;
  font-size: 1.2rem;
  height: 40px;
  border: none;
  padding: 0 15px;
  -webkit-user-select: auto;
     -moz-user-select: auto;
      -ms-user-select: auto;
          user-select: auto;
}

input::-ms-clear {
  display: none;
}

a {
  text-decoration: none;
}

.Loader {
  color: #ffffff;
  font-size: 10px;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  text-indent: -9999em;
  -webkit-animation: load 1s infinite linear;
          animation: load 1s infinite linear;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
}

